/* eslint-disable eqeqeq */
import { Ticker } from '@pixi/ticker';
import { EventEmitter } from '@pixi/utils';
import { IAudio, IAudioOption } from 'vfInterface';
import { AudioEngine } from './AudioEngine';
import { readFileSync } from 'packages/utils/src/network/readFileSync';

/**
 * WebAudio
 *
 * @class
 * @extends PIXI.utils.EventEmitter
 * @memberof PIXI
 */
export class WebAudio extends EventEmitter implements IAudio {
    /**
     * é³é¢å¨å±å¯ä¸å
     * @readonly
     * @member {string}
     */
    public readonly name: string;
    /**
     * æ¯å¦èªå¨æ­æ¾
     * @readonly
     * @default false
     * @member {string}
     */
    public readonly autoplay: boolean;
    /**
     * å®ææ­æ¾ä¸æ¬¡åå£°é³æ¯å¦å¾ªç¯æ­æ¾ã
     */
    public loop = false;
    /**
     * æ­¤å£°é³å½åæ¯å¦æ­æ¾ã
     */
    private _isPlaying = false;

    /**
     * audioEngineå®ä¾
     */
    private _ae: AudioEngine;
    private _isPause = false;
    private _playbackRate = 1;
    private _startTime = 0;
    private _startOffset = 0;
    private _playLength = 0;
    private _soundGain: GainNode;
    private _inputAudioNode: AudioNode;
    private _outputAudioNode: AudioNode;
    private _isReadyToPlay = false;
    private _volume = 1;
    private _audioBuffer: AudioBuffer;
    private _soundSource: AudioBufferSourceNode;
    private _isOutputConnected = false;
    private _playTimeId = -1;

    /**
     * è·åå½åé³é¢çé¿åº¦
     * @readonly
     * @default 0
     * @member {number}
     */
    public get duration(): number {
        if (this._audioBuffer) {
            return this._audioBuffer.duration;
        }

        return 0;
    }
    /**
     * è·åå½åé³é¢çæåç¶æ
     * @readonly
     * @default 1
     * @member {number}
     */
    public get paused(): boolean {
        return this._isPause;
    }
    /**
     * æ­æ¾çéç
     * @default 1
     * @member {number}
     */
    public get playbackRate(): number {
        return this._playbackRate;
    }
    public set playbackRate(value) {
        this._playbackRate = value;
    }

    constructor(name: string, urlOrArrayBuffer: any, options?: IAudioOption) {
        super();
        const audioEngine = this._ae = AudioEngine.Ins();

        // eslint-disable-next-line no-eq-null
        if (!audioEngine.canUseWebAudio || audioEngine.audioContext == null) {
            // eslint-disable-next-line no-console
            console.log('Web Audio is not supported by your browser.');
            audioEngine.WarnedWebAudioUnsupported = true;

            return;
        }
        this.name = name;

        if (options) {
            this.autoplay = options.autoplay || false;
            this.loop = options.loop || false;
            this._volume = options.volume || 0.7;
            this._playbackRate = options.playbackRate || 1;
        }

        this._soundGain = audioEngine.audioContext.createGain();
        this._soundGain.gain.value = this._volume;
        this._inputAudioNode = this._soundGain;
        this._outputAudioNode = this._soundGain;

        this.init(urlOrArrayBuffer);
    }

    private onTimeupdate(): void{
        const audioContext = this._ae.audioContext;

        if (this._isReadyToPlay && this._isPlaying && audioContext) {
            // eslint-disable-next-line max-len
            this.emit('timeupdate', this, this._startOffset + (audioContext.currentTime - this._startTime));
        }
    }

    private async init(urlOrArrayBuffer: any): Promise<void> {
        let validParameter = true;
        const audioEngine = this._ae;

        if (urlOrArrayBuffer) {
            try {
                if (typeof (urlOrArrayBuffer) === 'string' && audioEngine.checkCanPlayType(urlOrArrayBuffer)) {
                    const urlType = urlOrArrayBuffer.substr(0, 10);

                    if (urlType.indexOf('data:') === -1 || urlType.indexOf('http') > -1) {
                        // eslint-disable-next-line max-len
                        const data = await readFileSync(urlOrArrayBuffer, { responseType: 'arraybuffer' }).catch((e) => { console.error(e); });

                        if (data) {
                            this._soundLoaded(data);
                        } else {
                            validParameter = false;
                        }
                    } else {
                        const start = urlOrArrayBuffer.indexOf(',') + 1;
                        const base64Data = urlOrArrayBuffer.substr(start);
                        const urlData = `data:application/octet-binary;base64,${base64Data}`;
                        // eslint-disable-next-line max-len
                        const buffer = await readFileSync(urlData, { responseType: 'arraybuffer' }).catch((e) => { console.error(e); });

                        this._soundLoaded(buffer);
                    }
                } else if (urlOrArrayBuffer instanceof ArrayBuffer && urlOrArrayBuffer.byteLength > 0) {
                    if (urlOrArrayBuffer.byteLength > 0) {
                        this._soundLoaded(urlOrArrayBuffer);
                    }
                } else {
                    validParameter = false;
                }

                if (!validParameter) {
                    // eslint-disable-next-line max-len
                    console.error('Parameter must be a URL to the sound, an Array of URLs (.mp3 & .ogg) or an ArrayBuffer of the sound.');
                }
            }
            catch (ex) {
                // eslint-disable-next-line no-console
                console.log('Unexpected error. Sound creation aborted.');
            }
        }
    }

    private _soundLoaded(audioData: ArrayBuffer): void {
        if (!this._ae.audioContext) {
            return;
        }
        const onCanplaythrough = this.onCanplaythrough.bind(this);
        const onError = this.onError.bind(this);

        this._ae.audioContext.decodeAudioData(audioData, onCanplaythrough, onError);
    }

    private onCanplaythrough(buffer: AudioBuffer): void {
        this._audioBuffer = buffer;
        this._isReadyToPlay = true;
        this.emit('canplaythrough', this);
        if (this.autoplay) { this.play(); }
    }

    private onError(error: any): void {
        error = `Error while decoding audio data for: ${this.name} / Error: ${error}`;
        if (error) {
            console.error(error);
        }
        this.emit('error', this, error);
    }
    /**
     * å£°é³æ­æ¾æ¥å£
     *
     *  await sound.play()
     *
     * @param {number} [offset] - å£°é³çå¼å§åç§»å¼
     * @param {number} [length] - æç»­æ¶é´ï¼ä»¥ç§ä¸ºåä½ï¼
     */
    public play(when?: number, offset?: number, length?: number): void {
        const audioContext = this._ae.audioContext;

        if (this._isReadyToPlay && audioContext) {
            if (!this._isPause) {
                this._startOffset = offset || 0;
                this._playLength = length || 0;
            }
            try {
                const tryToPlay = (): void => {
                    let soundSource = this._soundSource;

                    if (soundSource) {
                        soundSource.stop();
                        soundSource.disconnect();
                    }
                    if (audioContext) {
                        const time = this._startOffset;
                        const playLength = this._playLength;

                        soundSource = audioContext.createBufferSource();
                        soundSource.buffer = this._audioBuffer;
                        soundSource.connect(this._inputAudioNode);
                        // soundSource.connect(audioContext.destination);
                        soundSource.loop = this.loop;
                        soundSource.playbackRate.value = this._playbackRate;
                        soundSource.onended = (): void => { this.onended(); };
                        soundSource.start(when, time, playLength || soundSource.buffer.duration);
                        this._soundSource = soundSource;
                        this._startTime = audioContext.currentTime;
                        this.emit('play', this);
                        if (this.listeners('timeupdate').length > 0) {
                            Ticker.shared.remove(this.onTimeupdate, this);
                            Ticker.shared.add(this.onTimeupdate, this);
                        }
                    }
                };

                let state1 = '';

                if (audioContext.state) {
                    state1 = audioContext.state.toString();
                }
                if (state1.indexOf('interrupted') !== -1) {
                    this._ae.emit(this._ae.audioLockedInterrupted, this);
                }
                else if (audioContext.state === 'suspended') {
                    window.clearTimeout(this._playTimeId);
                    // Wait a bit for FF as context seems late to be ready.
                    this._playTimeId = window.setTimeout(() => {
                        // eslint-disable-next-line
                        if (audioContext!.state === 'suspended') {
                            if (!this._ae.unlocked) {
                                this._ae.lock();
                                if (this.loop || this.autoplay) {
                                    this._ae.once(this._ae.audioUnlockedObservable, (): void => {
                                        tryToPlay();
                                    }, this);
                                }
                            } else if (this.loop || this.autoplay) {
                                tryToPlay();
                            }
                        }
                        else {
                            tryToPlay();
                        }
                    }, 500);
                }
                else {
                    tryToPlay();
                }
                this._isPlaying = true;
                this._isPause = false;
            }
            catch (e) {
                // eslint-disable-next-line no-console
                console.log(`Error while trying to play audio: ${this.name}, ${e.message}`);
            }
        }
    }

    private onended(): void {
        this._isPlaying = false;
        this.emit('ended', this);
    }

    /**
    * åæ­¢å£°é³
    * @param time (optional) Xç§ååæ­¢å£°é³ãé»è®¤æåµä¸ç«å³åæ­¢
    */
    public stop(time?: number): void {
        Ticker.shared.remove(this.onTimeupdate, this);
        if (this._isPlaying) {
            const audioEngine = this._ae;

            this._startOffset = 0;
            this._playLength = 0;
            if (audioEngine.audioContext && this._soundSource && this._soundSource.buffer) {
                try {
                    // eslint-disable-next-line max-len
                    const stopTime = time ? audioEngine.audioContext.currentTime + time : audioEngine.audioContext.currentTime;

                    this._soundSource.stop(stopTime);
                    if (!this._isPause) {
                        this._startOffset = 0;
                    }
                } catch (ex) {
                    // eslint-disable-next-line no-console
                    console.log(`Error sound to stop: ${this.name}, ${ex.message}`);
                }
            }
        }
    }

    /**
     * æåå£°é³
     */
    public pause(): void {
        Ticker.shared.remove(this.onTimeupdate, this);
        if (this._isPlaying) {
            const audioContext = this._ae.audioContext;

            this._isPause = true;
            if (audioContext) {
                const startOffset = this._startOffset;

                this.stop(0);
                this._startOffset = startOffset + audioContext.currentTime - this._startTime;
            }
            this.emit('pause', this, this.paused);
        }
    }

    /**
     * é³é(0.0 - 1.0)
     * @default 1
     * @member {number}
     */
    public get volume(): number {
        return this._volume;
    }
    public set volume(newVolume: number) {
        const audioEngine = this._ae;

        if (audioEngine.canUseWebAudio && this._soundGain) {
            this._soundGain.gain.value = newVolume;
        }
        this._volume = newVolume;
    }

    /**
     * éæ¾
     */
    public dispose(): void {
        this.removeAllListeners();
        Ticker.shared.remove(this.onTimeupdate, this);
        if (this._ae.canUseWebAudio) {
            if (this._isPlaying) {
                this.stop();
            }
            this._isReadyToPlay = false;
            if (this._soundSource) {
                this._soundSource.disconnect();
                this._soundSource = null;
            }
            this._audioBuffer = null;
        }
        this._ae.map.delete(this.name);
        this._ae = undefined;
        AudioEngine.Ins().map.delete(this.name);
    }

    /**
     * è·ååå«æ°æ®çå½ååºç¡é³é¢ç¼å²åº
     */
    public getAudioBuffer(): AudioBuffer {
        return this._audioBuffer;
    }

    /**
     * å°æ­¤å£°é³è¿æ¥å°å£°é³è½¨éé³é¢èç¹ï¼å¦å¢ç...
     * @param soundTrackAudioNode è¦è¿æ¥çé³è½¨é³é¢èç¹
     */
    public connectToSoundTrackAudioNode(soundTrackAudioNode: AudioNode): void {
        if (this._ae.canUseWebAudio) {
            if (this._isOutputConnected) {
                this._outputAudioNode.disconnect();
            }
            this._outputAudioNode.connect(soundTrackAudioNode);
            this._isOutputConnected = true;
        }
    }
}
