export function versionPrint(version: string, docsSite = 'https://github.com/vipkid-edu/vf-docs'): void
{
    if (navigator.userAgent.toLowerCase().indexOf('chrome') > -1)
    {
        const args = [
            `\n %c %c %c VFJS ${version} - â° ${renderType} â°  %c  %c  ${docsSite}  %c  %c \n\n`,
            'background: #ff9932; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'color: #ff66a5; background: #030307; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'color: #ff2424; background: #fff; padding:5px 0;',
        ];

        window.console.log(...args);
    }
    else if (window.console)
    {
        window.console.log(`VFJS ${version} - ${renderType} - ${docsSite}`);
    }
}

